import { FC, useEffect, useState } from "react";
import { Button, DatePicker, Form, Input, Select } from "antd";
import TextArea from "antd/lib/input/TextArea";
import moment from "moment";

import { MEETING_TYPES, MEETING_TYPES_OF_CAUSE } from "entities/meeting/types";
import { useStore } from "entities/store";
import { dateWithTimeFormat, getStringDate } from "features/dates";

const { Option } = Select;

interface IProps {
  requisites: any;
}

export const InfoBlock: FC<IProps> = ({ requisites }) => {
  const [isActiveForm, setIsActiveForm] = useState(true);
  const [form] = Form.useForm();

  const {
    changeMeetingInfo,
    company: {
      requisites: { address },
    },
  } = useStore((state) => state);

  const submitHandler = (values: any) => {
    changeMeetingInfo({ ...values, meeting_id: requisites.id });
    setIsActiveForm(!isActiveForm);
  };

  const activeHandler = () => {
    if (!requisites.address_of_meeting) {
      form.setFieldsValue({ address_of_meeting: address });
    }
    setIsActiveForm(!isActiveForm);
  };

  const onDateOfMeetingChange = (value: any) => {
    const stringDateOfMeeting = getStringDate(value);
    value.subtract(1, "hours");
    form.setFieldsValue({
      date_of_registration: value,
      date_of_meeting: moment(stringDateOfMeeting, dateWithTimeFormat),
    });
  };

  // const onAddressClick = () => {
  //   // console.log(address);
  //   form.setFieldsValue({ address_of_meeting: "dddd" });
  //   // form.setFieldsValue({ address_of_meeting: address });
  // };

  // useEffect(() => {

  // },[])

  const dates = "23.08.2022 14:00";

  if (!requisites.id) {
    return null;
  }

  return (
    <div>
      <Form
        form={form}
        layout="vertical"
        onFinish={submitHandler}
        initialValues={requisites}
        disabled={isActiveForm}
      >
        <Form.Item
          name="name"
          label="–£—Å–ª–æ–≤–Ω–æ–µ –Ω–∞–∑–≤–∞–Ω–∏–µ —Å–æ–±—Ä–∞–Ω–∏—è"
          rules={[
            {
              required: true,
              message: "Please input the title of collection!",
            },
          ]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          name="type_of_cause"
          label="–¢–∏–ø —Å–æ–±—Ä–∞–Ω–∏—è"
          rules={[
            {
              required: true,
              message: "Please input the title of collection!",
            },
          ]}
        >
          <Select allowClear style={{ width: "100%" }}>
            {Object.keys(MEETING_TYPES_OF_CAUSE).map((type) => (
              <Option key={type}>{MEETING_TYPES_OF_CAUSE[type]}</Option>
            ))}
          </Select>
        </Form.Item>
        <Form.Item
          name="type"
          label="–§–æ—Ä–º–∞ –ø—Ä–æ–≤–µ–¥–µ–Ω–∏—è"
          rules={[
            {
              required: true,
              message: "Please input the title of collection!",
            },
          ]}
        >
          <Select
            allowClear
            style={{ width: "100%" }}
            // onChange={handleChange}
          >
            {Object.keys(MEETING_TYPES).map((type) => (
              <Option key={type} value={type}>
                {MEETING_TYPES[type]}
              </Option>
            ))}
          </Select>
        </Form.Item>

        <Form.Item
          name="date_of_meeting"
          label="–î–∞—Ç–∞ –∏ –≤—Ä–µ–º—è –ø—Ä–æ–≤–µ–¥–µ–Ω–∏—è —Å–æ–±—Ä–∞–Ω–∏—è"
          rules={[
            {
              required: true,
              message: "Please input the title of collection!",
            },
          ]}
        >
          <DatePicker
            placeholder=""
            showTime={{ format: "HH:mm", minuteStep: 10 }}
            showNow={false}
            showSecond={false}
            format={dateWithTimeFormat}
            onChange={onDateOfMeetingChange}
          />
        </Form.Item>
        <Form.Item
          name="date_of_registration"
          label="–î–∞—Ç–∞ –∏ –≤—Ä–µ–º—è –Ω–∞—á–∞–ª–∞ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏ —É—á–∞—Å—Ç–Ω–∏–∫–æ–≤"
          rules={[
            {
              required: true,
              message: "Please input the title of collection!",
            },
          ]}
        >
          <DatePicker
            placeholder=""
            showTime={{ format: "HH:mm", minuteStep: 10 }}
            showNow={false}
            showSecond={false}
            format={dateWithTimeFormat}
          />
        </Form.Item>
        <Form.Item
          name="address_of_meeting"
          label="–ê–¥—Ä–µ—Å –ø—Ä–æ–≤–µ–¥–µ–Ω–∏—è —Å–æ–±—Ä–∞–Ω–∏—è"
          rules={[
            {
              required: true,
              message: "Please input the title of collection!",
            },
          ]}
        >
          <Input />
        </Form.Item>
        {!isActiveForm && (
          <Form.Item>
            <Button type="primary" htmlType="submit">
              –°–æ—Ö—Ä–∞–Ω–∏—Ç—å
            </Button>
          </Form.Item>
        )}
      </Form>
      {isActiveForm ? (
        <Button type="primary" onClick={activeHandler}>
          –ò–∑–º–µ–Ω–∏—Ç—å
        </Button>
      ) : null}
    </div>
  );
};
