import { Breadcrumb, Tabs } from "antd";
import { FC, useEffect } from "react";
import { NavLink, useParams } from "react-router-dom";

import { useStore } from "entities/store";

import { QuestionsBlock } from "./blocks/questionsBlock";
import { InfoBlock } from "./blocks/infoBlock";
import { DocumentsBlock } from "./blocks/documents";
import { AnswersBlock } from "./blocks/answersBlock";
import { MeetingBlock } from "./blocks/meetingBlock";

import "./index.scss";

const { TabPane } = Tabs;

const MeetingDetailsPage: FC = () => {
  const { id } = useParams();

  const { company, meeting, getMeeting, getGatalog, getCompany } = useStore(
    (state) => state
  );

  useEffect(() => {
    if (id) {
      getMeeting(id);
      getGatalog("question_type");
    }
  }, [id]);

  useEffect(() => {
    if (!company.id && meeting.info.company_id) {
      getCompany(meeting.info.company_id!);
    }
  }, [meeting]);

  if (!meeting) {
    return null;
  }

  return (
    <div>
      <Breadcrumb>
        <Breadcrumb.Item>
          <NavLink to="/companies">–ì–ª–∞–≤–Ω–∞—è</NavLink>
        </Breadcrumb.Item>
        <Breadcrumb.Item>
          <NavLink to="/companies">–ö–æ–º–ø–∞–Ω–∏–∏</NavLink>
        </Breadcrumb.Item>
        <Breadcrumb.Item>
          <NavLink to={"/companies/" + company?.id}>
            {company?.requisites.name}
          </NavLink>
        </Breadcrumb.Item>
      </Breadcrumb>
      <div className="header">
        <h1>–°–æ–±—Ä–∞–Ω–∏–µ: {meeting.info.name}</h1>
      </div>
      <Tabs defaultActiveKey="1">
        <TabPane tab="–ò–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è" key="1">
          <InfoBlock requisites={meeting.info} />
        </TabPane>
        <TabPane tab="–ü–æ–≤–µ—Å—Ç–∫–∞" key="2">
          <QuestionsBlock
            questions={meeting.questions!}
            meeting_id={meeting.id}
          />
        </TabPane>
        <TabPane tab="–î–æ–∫—É–º–µ–Ω—Ç—ã" key="3">
          <DocumentsBlock meeting={meeting} company={company} />
        </TabPane>
        <TabPane tab="–ì–æ–ª–æ—Å–æ–≤–∞–Ω–∏–µ" key="4">
          <AnswersBlock
            questions={meeting.questions!}
            meetingId={meeting.id}
            members={company.members}
            meeting={meeting}
          />
        </TabPane>
        <TabPane tab="–°–æ–±—Ä–∞–Ω–∏–µ" key="5">
          <MeetingBlock meeting={meeting} company={company} />
        </TabPane>
      </Tabs>
    </div>
  );
};

export default MeetingDetailsPage;
