import { IMember, MemberTypeEnum } from "entities/member/types";

export const getAnswersFromFormValues = (values: any) => {
  return Object.keys(values)
    .filter((item) => item !== "member_id")
    .map((filterItem) => ({
      question_id: +filterItem,
      member_id: values.member_id,
      result: values[filterItem],
    }));
};

export const getCurrentMembers = (members: IMember[], answers: any[]) => {
  console.log("üöÄ ~ file: utils.ts ~ line 14 ~ getCurrentMembers ~ members", members,answers )
  const answeredMembers = answers.map((answer) => answer.member_id);
  return members.filter(
    (member) =>
      member.type &&
      member.type.includes(MemberTypeEnum.owner) &&
      !answeredMembers.includes(member.id)
  );
};
