import { Alert, Button, Card } from "antd";
import { FC, useCallback, useState } from "react";
import AnswerForm from "entities/answer/form";
import { useModal } from "@ebay/nice-modal-react";
import { IMeeting, IQuestion } from "entities/meeting/types";
import { useStore } from "entities/store";
import { IMember } from "entities/member/types";
import { IAnswer, RESULT_TYPES } from "entities/answer/types";
import "./index.scss";
import { getFullNameFromMember } from "shared/utils/functions";

interface IProps {
  questions: IQuestion[];
  meetingId: string;
  members: IMember[];
  meeting: IMeeting;
}

const groupAnswersByMembers = (answers: IAnswer[], property: keyof IAnswer) => {
  return answers.reduce((acc: { [key: string]: any[] }, obj) => {
    // const key = obj['member_id'];
    const key = obj[property];
    if (!acc[key]) {
      acc[key as keyof typeof acc] = [];
    }
    acc[key as keyof typeof acc].push(obj);
    return acc;
  }, {});
};

const getObjectFromAnswersByMember = (
  answers: IAnswer[],
  member_id: number
) => {
  const filteredAnswersByMember = answers.filter(
    (answer) => answer.member_id === member_id
  );
  const result = filteredAnswersByMember.reduce((acc: any, cur) => {
    acc[cur.question_id] = "" + cur.result;
    return acc;
  }, {});
  return result;
};

export const AnswersBlock: FC<IProps> = ({
  meeting,
  meetingId,
  members,
  questions,
}) => {
  const userModal = useModal(AnswerForm);
  const { addAnswer, changeAnswer } = useStore((state) => state);

  const handleNewItem = useCallback(() => {
    userModal
      .show({ questions, members, answers: meeting.answers })
      .then((newItem: any) => {
        addAnswer(newItem);
        // setItems([newItem, ...items]);
        // addQuestion(newItem, meetingId)
      });
  }, [userModal, meeting.questions]);

  const handleChangeItem = useCallback(
    (changedItem: any) => {
      const answersForForm = getObjectFromAnswersByMember(
        meeting.answers,
        changedItem.member_id
      );
      const answer = { ...changedItem, ...answersForForm };
      userModal
        .show({ answer, members, questions, answers: meeting.answers })
        .then((newMember: any) => {
          changeAnswer(newMember);
        });
    },
    [userModal, questions]
  );

  const currentAnswersByMember = groupAnswersByMembers(
    meeting.answers,
    "member_id"
  );

  if (meeting.questions.length === 0) {
    return (
      <Alert
        message="–ù–µ —Å–æ–∑–¥–∞–Ω—ã –≤–æ–ø—Ä–æ—Å—ã –ø–æ–≤–µ—Å—Ç–∫–∏ –¥–Ω—è. –î–æ–±–∞–≤—å—Ç–µ –≤–æ–ø—Ä–æ—Å—ã –ø–æ–≤–µ—Å—Ç–∫–∏ –¥–Ω—è."
        type="warning"
      />
    );
  }

  return (
    <div>
      <Button type="primary" onClick={handleNewItem}>
        + –î–æ–±–∞–≤–∏—Ç—å –æ—Ç–≤–µ—Ç
      </Button>
      <div className="cards">
        {Object.keys(currentAnswersByMember).map((item) => {
          const member = members.find((member) => member.id === +item);
          return (
            <Card
              hoverable
              title={`${member && getFullNameFromMember(member)}, ${member?.part}%`}
              // title={`${member?.sirname} ${member?.firstname} ${member?.lastname}, ${member?.part}%`}
              key={member?.id}
              onClick={() => handleChangeItem({ member_id: +item })}
            >
              {currentAnswersByMember[item].map((answer) => {
                const question = meeting.questions.find(
                  (question) => question.id === answer.question_id
                );
                return (
                  <div key={answer.id}>
                    {question?.solution} -{" "}
                    <span className={"result-" + answer.result}>
                      {RESULT_TYPES[answer.result]}
                    </span>
                  </div>
                );
              })}
            </Card>
          );
        })}
      </div>
    </div>
  );
};
