import { Button, Card } from "antd";
import { createDocument, DOCUMENTS_TEMPLATES } from "features/createDocument";
import { FC, ReactNode, useState } from "react";
import { NavLink } from "react-router-dom";
import { DOCUMENT_TYPES, IDocumentsTypes } from "./types";

interface IProps {
  type: IDocumentsTypes;
  // Component: ReactNode;
  link: string;
}

export const DocumentCard: FC<IProps> = ({ type, link }) => {
  const [isPreview, setIsPreview] = useState(false);

  const previewHandler = () => {
    setIsPreview(!isPreview);
  };

  return (
    <Card title={DOCUMENT_TYPES[type]}>
      <div>
        <Button href={link} download type="link">
          –°–∫–∞—á–∞—Ç—å
        </Button>
        <Button type="link" onClick={previewHandler}>
          <NavLink to={"/templates/" + type}>–†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞—Ç—å</NavLink>
        </Button>
      </div>
    </Card>
  );
};
