import { Button, Card } from "antd";
import { FC, useCallback } from "react";
import { useModal } from "@ebay/nice-modal-react";

import QuestionForm from "entities/meeting/question";
import { IQuestion } from "entities/meeting/types";
import styles from "./styles.module.scss";
import { useStore } from "entities/store";

interface IProps {
  questions: IQuestion[];
  meeting_id: string;
}

export const QuestionsBlock: FC<IProps> = ({ questions, meeting_id }) => {

  const userModal = useModal(QuestionForm);
  const { addQuestion, changeQuestion } = useStore((state) => state);
  // const [items, setItems] = useState<any[]>([]);

  const handleNewItem = useCallback(() => {
    userModal.show().then((newItem: any) => {
      // setItems([newItem, ...items]);
      addQuestion({ ...newItem, meeting_id });
    });
  }, [userModal, questions]);

  const handleChangeItem = useCallback(
    (question: any) => {
      userModal.show({ question }).then((newMember: any) => {
        changeQuestion({...newMember, id: question.id})
      });
    },
    [userModal, questions]
  );

  // const { items, handleNewItem } = useItems<IQuestion>(QuestionForm);
  // console.log("üöÄ ~ file: index.tsx ~ line 28 ~ items", items);

  return (
    <div>
      <Button type="primary" onClick={handleNewItem}>
        + –î–æ–±–∞–≤–∏—Ç—å –≤–æ–ø—Ä–æ—Å
      </Button>
      <div className="cards">
        {questions.map((item) => {
          return (
            <Card
              hoverable
              title={item.name}
              key={item.id}
              onClick={() => handleChangeItem(item)}
            >
              <div className={styles.question_block}>
                <div className={styles.question_block_heading}>–†–µ—à–µ–Ω–∏–µ</div>
                <div>{item.solution}</div>
              </div>
              <div className={styles.question_block}>
                <div className={styles.question_block_heading}>–ü–æ—è—Å–Ω–µ–Ω–∏—è</div>
                <div>{item.description}</div>
              </div>
              <div className={styles.question_block}>
                <div className={styles.question_block_heading}>
                  –ù–µ–æ–±—Ö–æ–¥–∏–º–æ–µ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –≥–æ–ª–æ—Å–æ–≤
                </div>
                <div>{item.decision}</div>
              </div>
            </Card>
          );
        })}
      </div>
    </div>
  );
};
