import { FC, useState } from "react";
import { Alert, Button, DatePicker, Form } from "antd";

import { IMeeting } from "entities/meeting/types";
import { DocumentCard } from "entities/documents/DocumentCard";
import { IDocumentsTypes } from "entities/documents/types";
import { ICompanyFull } from "entities/company/types";
import { baseURL } from "shared/config/api";
import { dateWithTimeFormat } from "features/dates";
import { useStore } from "entities/store";

interface IProps {
  meeting: IMeeting;
  company: ICompanyFull;
}

export const MeetingBlock: FC<IProps> = ({ meeting, company }) => {
  const [isActiveForm, setIsActiveForm] = useState(true);
  const [form] = Form.useForm();

  const { changeMeetingFactInfo } = useStore((state) => state);

  const {
    date_of_meeting,
    date_of_registration,
    type,
    type_of_cause,
    address_of_meeting,
  } = meeting.info;

  const submitHandler = (values: any) => {
    changeMeetingFactInfo({ ...values, meeting_id: meeting.id });
    setIsActiveForm(!isActiveForm);
  };

  const activeHandler = () => {
    // if (!requisites.address_of_meeting) {
    //   form.setFieldsValue({ address_of_meeting: address });
    // }
    setIsActiveForm(!isActiveForm);
  };

  if (
    !date_of_meeting ||
    !date_of_registration ||
    !type ||
    !type_of_cause ||
    !address_of_meeting
  ) {
    return (
      <Alert
        message="–ù–µ –∑–∞–ø–æ–ª–Ω–µ–Ω–∞ –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è –ø—Ä–æ —Å–æ–±—Ä–∞–Ω–∏–µ. –ó–∞–ø–æ–ª–Ω–∏—Ç–µ –≤—Å–µ –ø—É–Ω–∫—Ç—ã –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü–µ –ò–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è."
        type="warning"
      />
    );
  } else {
    if (meeting.answers.length === 0) {
      return (
        <Alert
          message="–ï—â–µ –Ω–µ –ø—Ä–æ–π–¥–µ–Ω–æ –≥–æ–ª–æ—Å–æ–≤–∞–Ω–∏–µ –¥–ª—è —Å–æ–∑–¥–∞–Ω–∏—è –ø—Ä–æ—Ç–æ–∫–æ–ª–∞. –î–æ–±–∞–≤—å—Ç–µ –æ—Ç–≤–µ—Ç—ã –≤ –≥–æ–ª–æ—Å–æ–≤–∞–Ω–∏–µ."
          type="warning"
        />
      );
    }
  }

  return (
    <div>
      <div>
        <Form
          form={form}
          layout="vertical"
          onFinish={submitHandler}
          initialValues={meeting.info}
          disabled={isActiveForm}
        >
          <Form.Item
            name="date_of_start"
            label="–§–∞–∫—Ç–∏—á–µ—Å–∫–æ–µ –≤—Ä–µ–º—è –Ω–∞—á–∞–ª–∞ —Å–æ–±—Ä–∞–Ω–∏—è"
            rules={[
              {
                required: true,
                message: "Please input the title of collection!",
              },
            ]}
          >
            <DatePicker
              placeholder=""
              showTime={{ format: "HH:mm", minuteStep: 10 }}
              showNow={false}
              showSecond={false}
              format={dateWithTimeFormat}
            />
          </Form.Item>
          <Form.Item
            name="date_of_finish"
            label="–§–∞–∫—Ç–∏—á–µ—Å–∫–æ–µ –≤—Ä–µ–º—è –æ–∫–æ–Ω—á–∞–Ω–∏—è —Å–æ–±—Ä–∞–Ω–∏—è"
            rules={[
              {
                required: true,
                message: "Please input the title of collection!",
              },
            ]}
          >
            <DatePicker
              placeholder=""
              showTime={{ format: "HH:mm", minuteStep: 10 }}
              showNow={false}
              showSecond={false}
              format={dateWithTimeFormat}
            />
          </Form.Item>
          {!isActiveForm && (
            <Form.Item>
              <Button type="primary" htmlType="submit">
                –°–æ—Ö—Ä–∞–Ω–∏—Ç—å
              </Button>
            </Form.Item>
          )}
        </Form>
        {isActiveForm ? (
          <Button type="primary" onClick={activeHandler}>
            –ò–∑–º–µ–Ω–∏—Ç—å
          </Button>
        ) : null}
      </div>
      <div className="cards">
        <DocumentCard
          type={IDocumentsTypes.protocol}
          link={`${baseURL}/documents/${meeting.id}/${IDocumentsTypes.protocol}`}
        />
      </div>
    </div>
  );
};
function changeMeetingInfo(arg0: any) {
  throw new Error("Function not implemented.");
}
