import NiceModal, { useModal, antdModal } from "@ebay/nice-modal-react";
import { Form, Input, Modal, Select } from "antd";
import { ICompany } from "entities/company/types";
import { IQuestion } from "entities/meeting/types";
import { IMember, MemberTypeEnum } from "entities/member/types";
import { useCallback } from "react";
import { getFullNameFromMember } from "shared/utils/functions";
import { IAnswer, RESULT_TYPES } from "./types";
import { getAnswersFromFormValues, getCurrentMembers } from "./utils";

const { Option } = Select;

interface IProps {
  company: ICompany;
  questions: IQuestion[];
  members: IMember[];
  answer: IAnswer;
  answers: any[];
}

export default NiceModal.create(
  ({ company, questions, members, answer, answers }: IProps) => {
    const modal = useModal();
    const [form] = Form.useForm();

    const currentMembers =
      answer && answer.member_id
        ? members.filter((member) => member.id === +answer.member_id)
        : getCurrentMembers(members, answers);

    const handleSubmit = useCallback(() => {
      form.validateFields().then(() => {
        const values = { ...form.getFieldsValue() };
        console.log(
          "üöÄ ~ file: form.tsx ~ line 30 ~ form.validateFields ~ values",
          values
        );
        const answers = getAnswersFromFormValues(values);
        console.log(
          "üöÄ ~ file: form.tsx ~ line 32 ~ form.validateFields ~ answers",
          answers
        );
        modal.resolve(answers);
        modal.hide();
      });
    }, [
      modal,
      // user,

      form,
    ]);
    return (
      <Modal
        {...antdModal(modal)}
        title="–î–æ–±–∞–≤–∏—Ç—å –æ—Ç–≤–µ—Ç"
        okText="–°–æ—Ö—Ä–∞–Ω–∏—Ç—å"
        cancelText="–û—Ç–º–µ–Ω–∞"
        onOk={handleSubmit}
      >
        <Form form={form} layout="vertical" initialValues={answer}>
          <Form.Item
            name="member_id"
            label="–£—á–∞—Å—Ç–Ω–∏–∫"
            rules={[
              {
                required: true,
                message: "–ù–µ–æ–±—Ö–æ–¥–∏–º–æ –∑–∞–ø–æ–ª–Ω–∏—Ç—å –¥–∞–Ω–Ω–æ–µ –ø–æ–ª–µ",
              },
            ]}
          >
            <Select
              allowClear
              style={{ width: "100%" }}
              // onChange={handleChange}
            >
              {currentMembers.map((member) => (
                <Option key={member.id} value={member.id}>
                  {getFullNameFromMember(member)}
                </Option>
                // >{`${member.sirname} ${member.firstname} ${member.lastname}`}</Option>
              ))}
            </Select>
          </Form.Item>
          {questions.map((question) => {
            return (
              <Form.Item
                key={question.index}
                name={question.id}
                label={`–í–æ–ø—Ä–æ—Å ‚Ññ${question.index}. ${question.solution}`}
                rules={[
                  {
                    required: true,
                    message: "–ù–µ–æ–±—Ö–æ–¥–∏–º–æ –∑–∞–ø–æ–ª–Ω–∏—Ç—å –¥–∞–Ω–Ω–æ–µ –ø–æ–ª–µ",
                  },
                ]}
              >
                <Select
                  allowClear
                  style={{ width: "100%" }}
                  // onChange={handleChange}
                >
                  {Object.keys(RESULT_TYPES).map((item, index) => (
                    <Option key={index} value={item}>
                      {RESULT_TYPES[item]}
                    </Option>
                  ))}
                </Select>
              </Form.Item>
            );
          })}
        </Form>
      </Modal>
    );
  }
);
